import React from 'react';
import classes from './Footer.module.css';

const footer = () => {
  return (
    <div className={classes.footer}>
      <div className={classes.container}>
        <div className={classes.row}>
          {/*Columna 1 */}
          <div className={classes.column}>
            <ul className={classes.list}>
              <h2>Enlaces</h2>
              <li>
                <a href='https://www.ith.mx/'>Pagina ITH </a>
              </li>
              <li>
                <a href='https://www.ith.mx/'>Profesores </a>
              </li>
              <li>
                <a href='https://www.ith.mx/'>Otra pagina </a>
              </li>
            </ul>
          </div>
          {/*Columna 2 */}
          <div className={classes.column}>
            <ul className={classes.list}>
              <h2>Otra cosa</h2>
              <li>
                <a href='https://www.ith.mx/'>Pagina ITH </a>
              </li>
              <li>
                <a href='https://www.ith.mx/'>Profesores </a>
              </li>
              <li>
                <a href='https://www.ith.mx/'>Otra pagina </a>
              </li>
            </ul>
          </div>
          <hr />
          <div className={classes.row}>
            <p className={classes.colsm}>
              Copyright &copy; {new Date().getFullYear()} Instituto Tecnologico
              de Hermosillo. Algunos derechos reservados.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default footer;
