import React, { Fragment } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import TemporaryDrawer from '../SideDrawer/TemporaryDrawer';
import { NavLink } from 'react-router-dom';
import TecNM from '../../../assets/images/Logo-TecNM.png';
import ITH from '../../../assets/images/Logo-ITH.jpg';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'inline-block',
  },
  img: {
    padding: '5px',
    width: '150px',
  },
}));

let navLinkStyle = {
  color: 'black',
  textDecoration: 'none',
};

export default function MenuAppBar(props) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  let navigationItems = (
    <Fragment>
      <TemporaryDrawer drawerAuth={props.isAuth} />
      <h5 className={classes.title}>Control de capacitaciones</h5>
      <div>
        <IconButton
          aria-label='account of current user'
          aria-controls='menu-appbar'
          aria-haspopup='true'
          onClick={handleMenu}
          color='inherit'
        >
          <AccountCircle />
        </IconButton>
        <Menu
          id='menu-appbar'
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          keepMounted
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          open={open}
          onClose={handleClose}
        >
          {/*Opciones de la barra de herramienta: Perfil y cerrar sesion*/}
          <NavLink to='/profile-config' style={navLinkStyle}>
            <MenuItem onClick={handleClose}>Perfil</MenuItem>
          </NavLink>
          <NavLink to='/logout' style={navLinkStyle}>
            <MenuItem onClick={handleClose}>Cerrar sesion</MenuItem>
          </NavLink>
        </Menu>
      </div>
    </Fragment>
  );

  let toolBarIcons = (
    <div
      style={{
        display: 'flex',
        justifyContent: 'space-between',
        whiteSpace: 'nowrap',
      }}
    >
      <h5 className={classes.title}>Control de capacitaciones</h5>
      <div
        style={{
          display: 'inline-block',
          marginLeft: '330%',
          position: 'sticky',
        }}
      >
        <img className={classes.img} src={TecNM} alt='logo' />
      </div>
      {/* <div style={{ display: 'inline-block', marginLeft: '70%' }}>
        <img className={classes.img2} src={ITH} alt='logo' />
      </div> */}
    </div>
  );

  if (!props.isAuth) {
    navigationItems = null;
  }

  if (props.isAuth) {
    toolBarIcons = null;
  }

  return (
    <div className={classes.root}>
      <AppBar position='static' style={{ backgroundColor: '#304671' }}>
        <Toolbar>
          {toolBarIcons}
          {navigationItems}
        </Toolbar>
      </AppBar>
    </div>
  );
}
