import React, { Component, lazy, Suspense } from 'react';
import { connect } from 'react-redux';
import PrivateRoute from './pages/Auth/PrivateRoute/PrivateRoute';
import Layout from './hoc/Layout/Layout';
import Logout from './pages/Auth/Logout/Logout';
import NotFound from './pages/Auth/NotFound/NotFound';
import * as actions from './store/actions/index';
import { Route, Switch, Redirect, withRouter } from 'react-router-dom';

const Login = lazy(() => {
  return import('./pages/Auth/Login');
});

const ProfileConfig = lazy(() => {
  return import('./pages/ProfileConfig/ProfileConfig');
});

const ChangePassword = lazy(() => {
  return import('./pages/ProfileConfig/ChangePassword/ChangePassword');
});

const UsersOverview = lazy(() => {
  return import('./pages/Users/UsersOverview');
});

const CoursesOverview = lazy(() => {
  return import('./pages/Courses/CoursesOverview');
});

const CourseEnrollment = lazy(() => {
  return import('./pages/CourseEnrollment/CourseEnrollment');
});

const MyCourses = lazy(() => {
  return import('./pages/MyCourses/MyCourses');
});

const AttendancePartaker = lazy(() => {
  return import('./pages/AttendanceRegister/Partaker/AttendancePartaker');
});

const InstructorCourses = lazy(() => {
  return import('./pages/InstructorCourses/InstructorCourses');
});

const AttendanceMenuInstructor = lazy(() => {
  return import(
    './pages/AttendanceRegister/Instructor/AttendanceMenuInstructor'
  );
});

const AttendanceInstructor = lazy(() => {
  return import('./pages/AttendanceRegister/Instructor/AttendanceInstructor');
});

const PartakerList = lazy(() => {
  return import('./pages/InstructorCourses/PartakerList/PartakerList');
});

const GradesList = lazy(() => {
  return import('./pages/InstructorCourses/GradesList/GradesList');
});

const FileExplorer = lazy(() => {
  return import('./pages/FileExplorer/FileExplorer');
});

const Charts = lazy(() => {
  return import('./pages/Charts/Charts');
});

class App extends Component {
  componentDidMount() {
    this.props.onTryAutoSignup();
  }

  render() {
    let routes = (
      <Suspense fallback={<div></div>}>
        <Switch>
          <Route exact path='/'>
            {' '}
            <Redirect to='/Login' />{' '}
          </Route>
          <Route exact path='/Login' component={Login} />
          <PrivateRoute
            exact
            path='/profile-config'
            component={ProfileConfig}
          />
          <PrivateRoute
            exact
            path='/profile-config/change-password'
            component={ChangePassword}
          />

          <PrivateRoute
            path='/course-enrollment'
            component={CourseEnrollment}
          />

          {/* Cursos del participante, asistencia, evaluacion, descargar cedula, dar de baja*/}
          <PrivateRoute path='/mycourses' component={MyCourses} />
          <PrivateRoute
            path='/attendance-partaker/:group'
            component={AttendancePartaker}
          />

          {/* Cursos del instructor, asistencia, lista de inscritos y calificaciones*/}
          <PrivateRoute
            path='/instructor-courses/'
            component={InstructorCourses}
          />
          <PrivateRoute
            path='/attendance-menu-instructor/:group'
            component={AttendanceMenuInstructor}
          />
          <PrivateRoute
            path='/attendance-instructor/:group/:date'
            component={AttendanceInstructor}
          />
          <PrivateRoute
            path='/partakers-list/:group'
            component={PartakerList}
          />

          <PrivateRoute path='/grades-list/:group' component={GradesList} />

          <PrivateRoute path='/logout' component={Logout} />
          {/* Muestra mensaje cuando se quiera dirigir a una pagina que no existe*/}
          <PrivateRoute component={NotFound} />
        </Switch>
      </Suspense>
    );

    if (this.props.userRole === 'COORDINADOR') {
      routes = (
        <Suspense fallback={<div></div>}>
          <Switch>
            <Route exact path='/'>
              {' '}
              <Redirect to='/Login' />{' '}
            </Route>
            <Route exact path='/Login' component={Login} />
            {/* MODULOS PARA EL COORDINADOR*/}
            <PrivateRoute path='/users-overview' component={UsersOverview} />
            <PrivateRoute
              path='/courses-overview'
              component={CoursesOverview}
            />
            <PrivateRoute path='/file-explorer' component={FileExplorer} />
            <PrivateRoute path='/charts' component={Charts} />
            {/* Configuraci√≥n de usuario*/}
            <PrivateRoute
              exact
              path='/profile-config'
              component={ProfileConfig}
            />
            <PrivateRoute
              exact
              path='/profile-config/change-password'
              component={ChangePassword}
            />
            {/* Inscripciones*/}

            <PrivateRoute
              path='/course-enrollment'
              component={CourseEnrollment}
            />

            {/* Cursos del participante, asistencia, evaluacion, descargar cedula, dar de baja*/}
            <PrivateRoute path='/mycourses' component={MyCourses} />
            <PrivateRoute
              path='/attendance-partaker/:group'
              component={AttendancePartaker}
            />

            {/* Cursos del instructor, asistencia, lista de inscritos y calificaciones*/}
            <PrivateRoute
              path='/instructor-courses/'
              component={InstructorCourses}
            />
            <PrivateRoute
              path='/attendance-menu-instructor/:group'
              component={AttendanceMenuInstructor}
            />
            <PrivateRoute
              path='/attendance-instructor/:group/:date'
              component={AttendanceInstructor}
            />
            <PrivateRoute
              path='/partakers-list/:group'
              component={PartakerList}
            />
            <PrivateRoute path='/grades-list/:group' component={GradesList} />
            <PrivateRoute path='/logout' component={Logout} />
            {/* Muestra mensaje cuando se quiera dirigir a una pagina que no existe*/}
            <PrivateRoute component={NotFound} />
          </Switch>
        </Suspense>
      );
    }

    return (
      <div>
        <Layout>{routes}</Layout>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    userRole: state.auth.userRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState()),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
