import React from 'react';
import { connect } from 'react-redux';
import NavigationItem from './NavigationItem/NavigationItem';
import Icon from '@material-ui/core/Icon';
import classes from './NavigationItems.module.css';

const navigationItems = (props) => (
  // Navegacion hacia perfil y cerrar sesion se encuentra en components/Toolbar/ButtonAppBar
  <ul className={classes.NavigationItems}>
    {props.userRole === 'COORDINADOR' ? (
      <>
        <NavigationItem link='/users-overview'>
          <Icon>supervisor_account</Icon>Registro de usuarios
        </NavigationItem>
        <NavigationItem link='/courses-overview'>
          <Icon>schedule</Icon>Registro de cursos
        </NavigationItem>
        <NavigationItem link='/file-explorer'>
          <Icon>folder_shared</Icon>Documentos
        </NavigationItem>
        <NavigationItem link='/charts'>
          <Icon>show_chart</Icon>Estadisticas
        </NavigationItem>
        <NavigationItem link='/course-enrollment'>
          <Icon>assignment_turned_in</Icon>Inscripciones
        </NavigationItem>
        <NavigationItem link='/mycourses'>
          <Icon>local_library</Icon>Mis cursos
        </NavigationItem>
        <NavigationItem link='/instructor-courses'>
          <Icon>emoji_people</Icon>Instructor
        </NavigationItem>
      </>
    ) : (
      <>
        <NavigationItem link='/course-enrollment'>
          <Icon>assignment_turned_in</Icon>Inscripciones
        </NavigationItem>
        <NavigationItem link='/mycourses'>
          <Icon>local_library</Icon>Mis cursos
        </NavigationItem>
        <NavigationItem link='/instructor-courses'>
          <Icon>emoji_people</Icon>Instructor
        </NavigationItem>
      </>
    )}
  </ul>
);

const mapStateToProps = (state) => {
  return {
    userRole: state.auth.userRole,
  };
};

export default connect(mapStateToProps)(navigationItems);
